	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://danmaq.com/image/misc/L2_s.jpg\" alt=\"てもて\"></p>\n<p>相変わらず雨ばかりでどうにもすっきりしない気分の日々が続いていますが、皆様お元気でいらっしゃいますでしょうか？そろそろいつ梅雨明けてもおかしくない気がするんですがねぇ。</p>\n<p>ちょっと内輪の流れでこなたL5化にチャレンジしてみたが正直無理くさそう。これじゃあ黒化というよりも単に鬱入ってるだけにも見えるし。仮にゼロから描き起こしても彼女の性格上どうしても『演じてる』感が出てきてどうにもorz</p>\n<p>てかそれ系統のアニメってひぐらしと空鍋しか見たことないんで勉強不足も否めないかな。(というよりアニメ自体あんまり見ていない)</p>","frontmatter":{"cover":null,"date":"2007-07-29T01:29:00+09:00","strDate":"2007/7/28","title":"てもて"}}},"pathContext":{"path":"/ja/blog/2007/07/29-てもて/","langKey":"ja"}}

